<template>
  <!--   -->
  <b-overlay :show="authLoading">
    <form @submit.prevent="resetPasswordHandler" :aria-busy="authLoading">
      <transition-group name="slide">
        <div v-if="serverErrors" key="serverErrors">
          <b-alert show variant="danger">
            <ul class="mb-0">
              <li
                v-for="(error, index) in serverErrors"
                :key="index"
                style="text-align: center"
              >
                {{ error.errorMsg }}
              </li>
            </ul>
          </b-alert>
        </div>
        <div v-if="successResponse" key="successResponse">
          <b-alert show variant="success">
            {{ successResponse }}
          </b-alert>
        </div>
      </transition-group>

      {{ successResponse }}
      <header class="form-header">
        <h5 class="form-title">Reset Password</h5>
        <p class="form-description">Write your new Password</p>
      </header>
      <AppInput
        :validation-name="$t('form.password')"
        :placeholder="$t('form.password')"
        type="password"
        autoComplete="new-password"
        required
        validation-rules="required|min:8"
        v-model="form.password"
      ></AppInput>
      <AppInput
        :validation-name="$t('form.password')"
        :placeholder="$t('form.repeatPassword')"
        type="password"
        autoComplete="off"
        required
        validation-rules="required|min:8"
        v-model="form.c_password"
      ></AppInput>
      <AppButton type="submit" block variant="primary" :loading="authLoading">
        Update Password
      </AppButton>
      <!-- <footer class="d-flex align-items-center justify-content-between mt-3">
        <button
          type="button"
          @click="$emit('openSignupForm')"
          class="bg-transparent border-0 p-0 btn-link"
        >
          Register
        </button>
        <button
          type="button"
          @click="$emit('openLoginForm')"
          class="bg-transparent border-0 p-0 btn-link"
        >
          Login
        </button>
      </footer> -->
    </form>
  </b-overlay>
</template>

<script>
export default {
  name: 'resetPassword',
  middleware: 'visitors-only',
  data() {
    return {
      form: {
        password: '',
        c_password: '',
        token: '',
        authLoading: false,
        serverErrors: null,
        successResponse: null,
      },
    };
  },
  methods: {
    async resetPasswordHandler() {
      try {
        this.authLoading = !this.authLoading;

        const { Data } = await this.$axios.$post('/resetpassword', this.form);

        this.serverErrors = null;
        this.successResponse = Data.message;
        this.authLoading = false;
        this.$router.push(this.localePath('/'))
      } catch (error) {
        this.serverErrors = error.response.data.Data;
        this.successResponse = null;
        this.authLoading = false;
      }
    },
  },
  created() {
    this.$route.query.token && (this.form.token = this.$route.query.token);
  },
};
</script>

<style lang="scss" scoped>
.form-header {
  margin-bottom: 25px;
}
.form-title {
  font-size: 18px;
}
.form-description {
  color: $secondaryTextColor;
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 0;
}
.form-message {
  display: flex;
  justify-content: center;
  button {
    border: none;
    margin: 0 10px;
  }
}
form {
  max-width: 400px;
  padding: 10px;
  margin: 100px auto;
}
</style>
